


















import { defineComponent, ref, computed } from '@vue/composition-api'

export default defineComponent({
  name: 'CollectorTabs',

  props: {
    grpc: {
      type: String,
      default: '',
    },
    http: {
      type: String,
      default: '',
    },
  },

  setup(props) {
    const tabs = ref()

    const otlpGrpc = computed(() => {
      return formatTemplate(otlpGrpcTpl, props.grpc)
    })

    const otlpHttp = computed(() => {
      return formatTemplate(otlpHttpTpl, props.http)
    })

    return { tabs, otlpGrpc, otlpHttp }
  },
})

function formatTemplate(format: string, ...args: any[]) {
  return format.replace(/{(\d+)}/g, function (match, number) {
    return typeof args[number] !== 'undefined' ? args[number] : match
  })
}

const otlpGrpcTpl = `
exporters:
  otlp:
    endpoint: {0}
    tls:
      insecure: true
`.trim()

const otlpHttpTpl = `
exporters:
  otlphttp:
    endpoint: {0}
    tls:
      insecure: true
`.trim()
